@import url('https://fonts.googleapis.com/css?family=Lato');

body {
  margin: 0;
  padding: 0;
  font-size: 12px;
  font-family: 'Lato', monospace;
  color: #fff;
  background-color: #f8fae3
}

input {
  border: 0;
  margin: 0;
  padding: 0;
}

.Login {
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

